<template>
  <view class="work-container">
    <uni-section v-if :title="workLogId ? '修改工作量' : '添加工作量'" type="line">
	<uni-forms ref="form" :rules="rules" :modelValue="form">
		<uni-forms-item label="日期" required>
			<uni-datetime-picker type="date" v-model="form.workDay"/>
		</uni-forms-item>
		<uni-forms-item label="小时" required>
			<uni-number-box v-model="form.amount" :step="0.5" :min="minValue" :max="maxValue" />
		</uni-forms-item>
		<uni-forms-item label="明细">
			<uni-easyinput type="textarea" v-model="form.workDetails" placeholder="请输入工作明细" />
		</uni-forms-item>
	</uni-forms>
	<view class="button-group">
		<button v-if="workLogId"  style="color: #ffffff; background-color: #ff0000;"  size="mini" @click="del">删除</button>
	    <button v-else style="color: #000000; background-color: #ffffff;" size="mini" @click="goBack()">返回</button>
		<button type="primary" size="mini" @click="submit">提交</button>
	</view>
	</uni-section>
  </view>
</template>

<script>
import {getWorkLogNoById,workLogNoAdd,workLogNoModify} from '@/api/work/archive/worklog'
export default {
  data () {
    return {
	  nowDay: Number(new Date()),
      workLogId: undefined,
	  maxValue:10,
	  minValue:0,
      form: {
		  workDay:undefined,
		  workDetails:'',
		  amount: 0.5,
	  },
      rules: {
		workDay: {
			rules:[{
				required: true,
				errorMessage: '请选择日期',
			}]
		},
		amount: {
			rules:[{
				required: true,
				errorMessage: '请输入小时',
			}]
		},
		workDetails: {
			rules:[{
				required: true,
				errorMessage: '请输入明细',
			}]
		},
      }
    }
  },
  onReady() {
	this.$refs.form.setRules(this.rules);
  },
  onLoad (params) {
    this.workLogId = params.id
    this.loadData()
  },
  methods: {
	// 表单重置
	reset() {
	  this.form = {
		workDay:'',
		amount: 0,
		workDetails:''
	  };
	  this.showOtherCount = false;
	},
    loadData () {
      if (this.workLogId) {
        const app = this
		getWorkLogNoById(this.workLogId).then(res => {
		  app.form = res.data
		})
      }else{
		this.form.workDay =this.nowDay;
	  }
    },
    submit() {
	  const app = this
	  this.$refs.form.validate().then(res => {
        if (app.workLogId) {
          workLogNoModify(app.form).then(res => {
            uni.showToast({ title: '修改成功！' })
			app.reset();
			uni.navigateBack({ delta: 1});
          })
        } else {
          workLogNoAdd(app.form).then(res => {
			uni.showToast({ title: '提交成功！' })
			app.reset();
			uni.navigateBack({ delta: 1});
          })
        }
      });
    },
    goBack () {
		uni.navigateBack({ delta: 1});
    },
	del () {
		uni.showToast({ title: '请联系管理员进行删除！' })
	},
  }
}
</script>
<style lang="scss">
.button-group {
	margin-top: 15px;
	display: flex;
	justify-content: space-around;
}

.form-item {
	display: flex;
	align-items: center;
}

.button {
	display: flex;
	align-items: center;
	height: 20px;
	margin-left: 10px;
}
</style>